import React, { useContext } from "react";

import { DispatchContext } from "./context/SettingsContext";
import { SettingsContext } from "./context/SettingsContext";

import Button from "@material-ui/core/Button";

function DrawerContent(props) {
    const { openControl } = props;
    const settings = useContext(SettingsContext);
    const dispatch = useContext(DispatchContext);
    const {
        app: { showAniControl },
    } = settings;
    const handleBtnClick = e => {
        openControl(e.currentTarget.value);
    };

    const removeWeapon = e => {
        const target = e.currentTarget.value;
        const value = {};
        value[`weapon${target}`] = "";
        const action = {
            type: "update",
            key: "model",
            value,
        };
        dispatch(action);
    };
    const toggleAniControl = () => {
        const action = {
            type: "update",
            key: "app",
            value: { showAniControl: !showAniControl },
        };
        dispatch(action);
    };

    return (
        <>
            <p
                style={{
                    color: "white",
                    backgroundColor: "rgba(0,0,0,0.8)",
                    width: "100%",
                    textAlign: "center",
                    padding: "0.5rem auto",
                }}
            >
                Under Construction
            </p>
            <Button variant="contained" value="model" onClick={handleBtnClick}>
                Choose a model
            </Button>
            <Button
                variant="contained"
                value="faceOverride"
                onClick={handleBtnClick}
            >
                Override Face
            </Button>
            <Button variant="contained" value="face" onClick={handleBtnClick}>
                Choose Face
            </Button>
            <Button
                variant="contained"
                value="animation"
                onClick={handleBtnClick}
            >
                Choose Animation
            </Button>
            <Button
                variant="contained"
                value="background"
                onClick={handleBtnClick}
            >
                Background Settings
            </Button>
            <Button variant="contained" value="weapon" onClick={handleBtnClick}>
                Weapons
            </Button>
            <Button variant="contained" value="Left" onClick={removeWeapon}>
                Remove Left Weapon
            </Button>
            <Button variant="contained" value="Right" onClick={removeWeapon}>
                Remove Right Weapon
            </Button>
            <Button
                variant="contained"
                value="Right"
                onClick={toggleAniControl}
            >
                Toggle Animation Control
            </Button>

            <p>Right click and drag or swipe with 3 fingers to pan</p>
        </>
    );
}

export default DrawerContent;
