import React, { useState } from "react";

import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import WeaponAni from "./WeaponAni";
import UniqueAni from "./UniqueAni";

import "./styles/AnimationSelect.css";

import { commonAnimation, skills, ingameOthers } from "./data/animationList";
import { aniButtonsFromObject } from "./helpers";

function GameAni({ handleSelect }) {
    const [category, setCategory] = useState(0);

    const handleChange = (_, value) => {
        setCategory(value);
    };

    let content;
    switch (category) {
        case 0: // Common animations
            content = aniButtonsFromObject(commonAnimation, handleSelect);
            break;
        case 1: // Standby animations
            content = <WeaponAni handleSelect={handleSelect} />;
            break;
        case 2: // Victory animations
            content = <UniqueAni handleSelect={handleSelect} />;
            break;
        case 3: // Skill animations
            content = aniButtonsFromObject(skills, handleSelect);
            break;
        case 4: // Other animations
            content = aniButtonsFromObject(ingameOthers, handleSelect);
            break;
        default:
    }

    return (
        <div className="AnimationSelect-categories">
            <Tabs
                orientation="vertical"
                variant="scrollable"
                value={category}
                onChange={handleChange}
            >
                <Tab label="Common Actions" />
                <Tab label="Weapon Specific" />
                <Tab label="Unique" />
                <Tab label="Skills" />
                <Tab label="Others" />
            </Tabs>
            <div className="AnimationSelect-subCategory">{content}</div>
        </div>
    );
}

export default GameAni;
