import React, { useState } from "react";
import InputLabel from "@material-ui/core/InputLabel";
import MenuItem from "@material-ui/core/MenuItem";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import Button from "@material-ui/core/Button";

import { getStandbyCode, getVictoryCode } from "./helpers";

import "./styles/AnimationSelect.css";

function WeaponAni({ handleSelect }) {
    const [params, setParams] = useState({ weapon: "Sword", gender: "Male" });

    const handleChange = event => {
        const { name, value } = event.target;
        setParams(oldParams => {
            return { ...oldParams, [name]: value };
        });
    };

    return (
        <div className="AnimationSelect-Standby">
            <FormControl variant="outlined">
                <InputLabel>Weapon</InputLabel>
                <Select
                    value={params.weapon}
                    onChange={handleChange}
                    label="Weapon"
                    name="weapon"
                    style={{ minWidth: "7rem" }}
                >
                    <MenuItem value="Sword">Sword</MenuItem>
                    <MenuItem value="Blade">Blade</MenuItem>
                    <MenuItem value="Dagger">Dagger</MenuItem>
                    <MenuItem value="Axe">Axe</MenuItem>
                    <MenuItem value="Lance">Lance</MenuItem>
                    <MenuItem value="Bow">Bow</MenuItem>
                    <MenuItem value="Wand">Wand</MenuItem>
                    <MenuItem value="Staff">Staff</MenuItem>
                </Select>
            </FormControl>
            <FormControl variant="outlined">
                <InputLabel>Gender</InputLabel>
                <Select
                    value={params.gender}
                    onChange={handleChange}
                    label="Gender"
                    name="gender"
                    style={{ minWidth: "7rem" }}
                >
                    <MenuItem value="Male">Male</MenuItem>
                    <MenuItem value="Female">Female</MenuItem>
                </Select>
            </FormControl>
            <Button
                variant="contained"
                data-value={getStandbyCode(params.weapon, params.gender)}
                onClick={handleSelect}
            >
                Standby Animation
            </Button>
            <Button
                variant="contained"
                data-value={getVictoryCode(params.weapon, params.gender)}
                onClick={handleSelect}
            >
                Victory Animation
            </Button>
        </div>
    );
}

export default WeaponAni;
