import React from "react";

import Button from "@material-ui/core/Button";

import { aniButtonsFromObject } from "./helpers";
import { uniqueWins } from "./data/animationList";

import "./styles/UniqueAni.css";

function UniqueAni({ handleSelect }) {
    const uniqueWinsBtns = aniButtonsFromObject(uniqueWins, handleSelect);
    return (
        <div>
            <Button
                variant="contained"
                data-value="lobby+ROD_ONT_21_11035401"
                onClick={handleSelect}
            >
                Mega Man Standby Animation
            </Button>
            <h3 style={{ width: "100%" }}>Unique Victory Animation</h3>
            <div className="UniqueAni-UniqueWins">{uniqueWinsBtns}</div>
        </div>
    );
}

export default UniqueAni;
